import {
  CustomerOrderModel,
  Money,
  Coupon,
  GiftCard,
  CustomerAddress,
  PaymentMethod,
  OrderItem,
  Shipment,
  ShipmentTracking,
  ShipmentComment,
  ShipmentItem,
  CustomerInfo,
  OrderTotal,
  OrderDiscount,
} from '@/b2b-purchase-order/data/models/customer-order-model';

export const transformCustomerOrder = (response: any): CustomerOrderModel => {
  const orderData = response.data?.placeOrderForPurchaseOrder?.order;

  const transformMoney = (money: any): Money => ({
    value: money?.value || 0,
    currency: money?.currency || '',
  });

  const transformCoupon = (coupon: any): Coupon => ({
    code: coupon?.code || '',
    label: coupon?.label || '',
  });

  const transformGiftCard = (giftCard: any): GiftCard => ({
    code: giftCard?.code || '',
    appliedBalance: transformMoney(giftCard?.applied_balance),
    currentBalance: transformMoney(giftCard?.current_balance),
  });

  const transformAddress = (address: any): CustomerAddress => ({
    firstname: address?.firstname || '',
    lastname: address?.lastname || '',
    street: address?.street || [],
    city: address?.city || '',
    region: address?.region || '',
    postcode: address?.postcode || '',
    countryCode: address?.country_code || '',
    telephone: address?.telephone || '',
    company: address?.company || '',
  });

  const transformPaymentMethod = (payment: any): PaymentMethod => ({
    name: payment?.name || '',
    type: payment?.type || '',
    additionalData: payment?.additional_data || {},
  });

  const transformOrderItem = (item: any): OrderItem => ({
    id: item?.id || '',
    productName: item?.product_name || '',
    productSku: item?.product_sku || '',
    quantityOrdered: item?.quantity_ordered || 0,
    quantityShipped: item?.quantity_shipped || 0,
    quantityInvoiced: item?.quantity_invoiced || 0,
    quantityRefunded: item?.quantity_refunded || 0,
    price: transformMoney(item?.price),
    total: transformMoney(item?.total),
  });

  const transformShipmentTracking = (tracking: any): ShipmentTracking => ({
    number: tracking?.number || '',
    carrier: tracking?.carrier || '',
    title: tracking?.title || '',
  });

  const transformShipmentComment = (comment: any): ShipmentComment => ({
    message: comment?.message || '',
    timestamp: comment?.timestamp || '',
  });

  const transformShipmentItem = (item: any): ShipmentItem => ({
    id: item?.id || '',
    productName: item?.product_name || '',
    productSku: item?.product_sku || '',
    quantityShipped: item?.quantity_shipped || 0,
  });

  const transformShipment = (shipment: any): Shipment => ({
    id: shipment?.id || '',
    number: shipment?.number || '',
    tracking: (shipment?.tracking || []).map(transformShipmentTracking),
    comments: (shipment?.comments || []).map(transformShipmentComment),
    items: (shipment?.items || []).map(transformShipmentItem),
  });

  const transformCustomerInfo = (customerInfo: any): CustomerInfo => ({
    firstname: customerInfo?.firstname || '',
    lastname: customerInfo?.lastname || '',
    email: customerInfo?.email || '',
  });

  const transformOrderDiscount = (discount: any): OrderDiscount => ({
    label: discount?.label || '',
    amount: transformMoney(discount?.amount),
  });

  const transformOrderTotal = (total: any): OrderTotal => ({
    baseGrandTotal: transformMoney(total?.base_grand_total),
    grandTotal: transformMoney(total?.grand_total),
    subtotal: transformMoney(total?.subtotal),
    totalTax: transformMoney(total?.total_tax),
    totalShipping: transformMoney(total?.total_shipping),
    discounts: (total?.discounts || []).map(transformOrderDiscount),
  });

  const transformOrderData = (data: any): CustomerOrderModel => ({
    appliedCoupons: (data?.applied_coupons || []).map(transformCoupon),
    appliedGiftCards: (data?.applied_gift_cards || []).map(transformGiftCard),
    availableActions: data?.available_actions || [],
    billingAddress: data?.billing_address
      ? transformAddress(data.billing_address)
      : {
          firstname: '',
          lastname: '',
          street: [],
          city: '',
          region: '',
          postcode: '',
          countryCode: '',
          telephone: '',
          company: '',
        },
    carrier: data?.carrier || '',
    comments: data?.comments || [],
    creditMemos: data?.credit_memos || [],
    customerInfo: data?.customer_info
      ? transformCustomerInfo(data.customer_info)
      : {
          firstname: '',
          lastname: '',
          email: '',
        },
    email: data?.email || '',
    giftMessage: data?.gift_message || '',
    giftReceiptIncluded: data?.gift_receipt_included || false,
    giftWrapping: data?.gift_wrapping || null,
    id: data?.id || '',
    invoices: data?.invoices || [],
    isVirtual: data?.is_virtual || false,
    items: (data?.items || []).map(transformOrderItem),
    itemsEligibleForReturn: data?.items_eligible_for_return || [],
    number: data?.number || '',
    orderDate: data?.order_date || '',
    orderStatusChangeDate: data?.order_status_change_date || '',
    paymentMethods: (data?.payment_methods || []).map(transformPaymentMethod),
    printedCardIncluded: data?.printed_card_included || false,
    returns: data?.returns || null,
    shipments: (data?.shipments || []).map(transformShipment),
    shippingAddress: data?.shipping_address
      ? transformAddress(data.shipping_address)
      : {
          firstname: '',
          lastname: '',
          street: [],
          city: '',
          region: '',
          postcode: '',
          countryCode: '',
          telephone: '',
          company: '',
        },
    shippingMethod: data?.shipping_method || '',
    status: data?.status || '',
    token: data?.token || '',
    total: data?.total
      ? transformOrderTotal(data.total)
      : {
          baseGrandTotal: transformMoney(null),
          grandTotal: transformMoney(null),
          subtotal: transformMoney(null),
          totalTax: transformMoney(null),
          totalShipping: transformMoney(null),
          discounts: [],
        },
  });

  return orderData ? transformOrderData(orderData) : transformOrderData(null);
};
