import { fetchGraphQl } from '@/b2b-purchase-order/api/fetch-graphql';
import { handleNetworkError } from '@/b2b-purchase-order/lib/network-error';
import { handleFetchError } from '@/b2b-purchase-order/lib/fetch-error';
import { PLACE_ORDER_FOR_PURCHASE_ORDER } from '@/b2b-purchase-order/api/placeOrderForPurchaseOrder/graphql/placeOrderForPurchaseOrder.graphql';
import { transformCustomerOrder } from '@/b2b-purchase-order/data/transforms/transform-customer-order';
import { CustomerOrderModel } from '@/b2b-purchase-order/data/models';

export const placeOrderForPurchaseOrder = async (
  purchaseOrderUid: string
): Promise<CustomerOrderModel> => {
  if (!purchaseOrderUid || purchaseOrderUid.trim() === '') {
    throw new Error('Purchase Order UID is required');
  }

  const input = {
    purchase_order_uid: purchaseOrderUid,
  };

  try {
    const response: any = await fetchGraphQl(PLACE_ORDER_FOR_PURCHASE_ORDER, {
      variables: { input },
    });

    if (response.errors?.length) {
      handleFetchError(response.errors);
    }

    return transformCustomerOrder(response);
  } catch (error) {
    throw handleNetworkError(error as Error);
  }
};
